
@import './fonts';

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

::-webkit-scrollbar {
  width: 10px;
  &-thumb {
    background: #888;
    border-radius: 10px;
    &:hover {
      background: #555;
    }
  }
  &-track {
    background: transparent;
    border-radius: 10px;
  }
}

.custom-scroll {
  max-height: calc(100vh - 7rem);
  padding-top: -5px;
  overflow-y: auto;
}

.collection {
  position: relative;
  display: flex;
  width: 17rem;
  height: 23.75rem;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.5rem;
  border-radius: 0.5rem;
  background-color: #F7F7F8;
  padding: 0.5rem 0.75rem 1rem 0.75rem;

  &-header {
    display: flex;
    padding-bottom: 0px;
    justify-content: space-between;
    align-items: flex-start;
    align-self: stretch;

    .collection-credential {
      gap: 0.375rem;
      flex-shrink: 0;
    }

    .collection-fav {
      width: 0.9375rem;
      height: 0.83331rem;
      flex-shrink: 0;
    }
  }

  &-cover {
    display: flex;
    padding-bottom: 0px;
    width: 15.75rem;
    height: 10rem;
    border-radius: 0.25rem;
    flex-direction: column;
    align-items: center;
    
    img {
      width: 15.75rem;
      height: 10rem;
      border-radius: 0.25rem;
      object-fit: cover;
    }
  }
  
  &-title {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    align-items: center;
    align-self: stretch;
    overflow: hidden;
    align-content: flex-start;
    color: var(--medium-grey, #505050);
    text-overflow: ellipsis;
    @extend .omni-font-l-medium;
  }
  &-description {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    flex: 1 0 0;
    align-items: center;
    align-content: flex-start;
    align-self: stretch;
    overflow: hidden;
    color: var(--medium-grey, #505050);
    font-feature-settings: 'clig' off, 'liga' off;
    text-overflow: ellipsis;
    @extend .omni-font-m-regular;
  }

  &-action {
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    align-self: stretch;
    padding-top: 0rem;

    .btn {
      display: flex;
      flex-direction: row;
      padding: 0.5rem 1rem;
      justify-content: center;
      align-items: center;
      align-content: center;
      border-radius: 0.375rem;
      font-feature-settings: 'clig' off, 'liga' off;
      cursor: pointer;
      @extend .omni-font-s-medium;

      &-primary {
        background: var(--accent-colour, #F19100);
        color: var(--white, #FFF);

        &:hover {
          background: #505050;
        }       
      }

      &-secondary {
        background: var(--light-grey-300, #DEDEDF);
        color: var(--medium-grey, #505050);

        &:hover {
          background: #e8e9eb;
          border: #505050;
          color: var(--accent-colour, #505050);
        }    
      }

      svg {
        margin-right: 0.5rem;
      }
      
    }
  }
}
